*{
    margin:0;
    padding:0;
    text-decoration: none;
    --white: #ffffff;
    --green1: #00ff00;
    --green2: #18c305;
    --green3: #169000;
    --green4: #0d5503;
    --black: #000000 ;
    --greenLowO:rgba(13, 85, 3, 0.055);
    font-family: "Bungee", serif;
    font-style: normal;
    letter-spacing: 0.2rem;
    src: url('https://fonts.googleapis.com/css2?family=Bungee&display=swap');
    
    
}

@font-face {
    font-family: "Bungee", serif;
    font-style: normal;
   
    src: url('https://fonts.googleapis.com/css2?family=Bungee&display=swap');
  }

li {
    list-style-type:none;
}

.liCustom {
    font-size: calc(0.8vw + 0.7vh + 0.5vmin);
    backdrop-filter: blur(5px);
    padding-bottom: 2rem;
    border-bottom: 1px solid green;
    color: #00ff00;
    background-color: rgba(13, 85, 3, 0.8);
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    align-self: center;
    align-content: center;
    width: auto;
}
.ubuntu-regular {
    font-family: "Ubuntu", sans-serif;
    font-weight: 400;
    font-style: normal;
  }
  
  .ubuntu-bold {
    font-family: "Ubuntu", sans-serif;
    font-weight: 700;
    font-style: normal;
  }
  
  .ubuntu-regular-italic {
    font-family: "Ubuntu", sans-serif;
    font-weight: 400;
    font-style: italic;
  }
  
  .ubuntu-bold-italic {
    font-family: "Ubuntu", sans-serif;
    font-weight: 700;
    font-style: italic;
  }




body {
    background: black;
    background-image: url(./assets/BOBBYBOOOST_BCKGRND_2.png);
   
    background-size: contain;
    background-clip: border-box;
    background-position: top;

}

.container{
    display:flex;
   
}
main{
    width: 100%;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    position: relative;
    justify-content: center;
    align-items: center;


}
.sidebar{
    z-index: 100;
    background: linear-gradient(-45deg, #005000c4, #18c3059d, #041a008a, #03380398);
    border-bottom: 2px solid var(--green1);
	background-size: 400% 400%;
	animation: gradient 20s ease infinite;
    color: #ffffff;
    height: 75px;
    width: 100%;
    
    transition: all 0.5s;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(1rem, 1fr)) ;
    gap: 0.5rem;
    align-items: center;
    justify-content: center;
    align-content: center;
    align-self: center;
    backdrop-filter: blur(6px);
    padding-left: 1rem;
    padding-right: 1rem;

}
.top_section{
    display: flex;
    display: flex;
    flex-direction: row;
    align-items:center;
    padding:20px 15px;
    align-items: center;
    max-width: 70px;
}
.logo{
    height: 100%;
    
    transition: all 300ms;
    width: 50px;
}
.bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
}

.logo:hover {
    transform: rotateY(360deg);
}


.link{
    display: flex;
    color: var(--white);
    padding: 10px ;
    max-width: 180px;
    transition: all 0.3s;
    align-self: center;
    align-items: center;
    justify-content: center;
    border-radius: 0.5rem;
}
.link:hover{
    background:var(--green1);
    color: #000000;
    transition: all 0.3s;
    border-radius: 0.5rem;
    font-weight: 700;
    border-bottom: 2px solid var(--green1);
}
.active{
    background: lightskyblue;
    color: #000;
    align-self: center;
    align-items: center;
    justify-content: center;
}
.icon, .link_text{
    font-size:1rem;
}


section {
  width: 98%;
    height: 90vh;
    margin-top: 6em;
    margin-bottom: 0.5em;
    background-color: #fffffffb;
    border-radius: 0.5rem;
    z-index: 50;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    border-bottom: 2px solid var(--green1);
    background-image: url(./assets/BOBBYBOOOST_BCKGRND_2_INVERTED.png);

}

.section2 {
    
    width: 98%;
    height: 90vh;
    margin-top: 6em;
    margin-bottom: 0.5em;
    background-color: #0d5503fb;
    border-radius: 0.5rem;
    z-index: 50;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    border-bottom: 2px solid var(--green1);
}

.section3 {
    width: 98%;
    height: 90vh;
    margin-top: 6em;
    margin-bottom: 0.5em;
    background: #fff6f6;
    border-bottom: 2px solid var(--green1);
	background-image: url(./assets/BOBBYBOOOST_BCKGRND_3_Section3.png);
    background-position: center;
    border-radius: 0.5rem;
    z-index: 50;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
}

.NavigationButtons {
    display: flex;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    align-items: center;
    align-content: center;
   
}

.bbbfallinganimation {
    width: 100vw;
    height: 60vh;
    position: absolute;
}

.HomeSection {
    background: none;
    display: flex;
    width: 98%;
    align-items: center;
    align-self: center;
    padding: 1rem;
    padding-top: 5rem;
   
}
.mainTitle {
    font-size: calc(3vw + 3vh + 2.5vmin);
    white-space: nowrap;
    align-content: center;
    align-items: center;
    align-self: center;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    color: #04ff00;
    animation: gradient 15s ease infinite;
}

.centeredMainText {
    align-items: center;
    justify-content: center;
    display: flex;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    align-content: center;
    align-self: center;
    position: absolute;
    top: 0;
    z-index: 1500;
}



@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.MainContainer {
  
    display: flex;
 
   flex-direction: column;
    align-items: center;
    align-self: center;
    left: 0;
    right: 0;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    height: 100%;
    width: 100%;
    
   
}

.HomeBobby {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    padding-left: 1rem;
    justify-content: space-around;
}

.BobbyContainer {
 
    width: 30%;
    position: relative;
    height: 80%;
    justify-content: center !important;
    align-items: center !important;
    align-self: center !important;
    display: flex;
   margin-top: -7%;
}

.parallaxIMG1 {
    

    width: 100%;
    height: 100%;
    z-index: 1;
}
.parallaxIMG2 {
    

    width: 100%;
    height: 100%;
    z-index: 1;
}

.bobbyCentered {
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    justify-content: center;
    align-items: center;
    align-self: center;
    display: flex;
    width: auto;
    z-index: 1000;
}

.SliderMain {
    display: flex;

}

.slider-container{
    width: 100%;
    justify-content: center;
    align-items: center;
    align-self: center;
    
}

.BobbyCarousel {
    width: 43%;
    height: auto;

}

.slick-next:before, .slick-prev:before {
    color: #00ff00;
    font-size: 30px;
}

.slick-next:hover, .slick-prev:hover {
    color: #ffffff !important;
    transform: scale(1.3) translateY(-0.5rem);
    transition: all 300ms;
   
}



.slick-dots li.slick-active button:before {
    color: #00ff00;
    font-size: 15px;
}

.slick-dots li button:before {
    color: #1fce08;
    font-size: 10px;
}

.slidersBackground {
    border: 2px solid #00ff00;
    height: 50vh;
    border-radius: 1rem;
    padding: 1rem;
    width: 92% !important;
    align-items: center;
    align-self: center;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    display: flex !important;
   justify-content: center;
   backdrop-filter: blur(5px);
   background-color: #00000086;
   align-content: center;
   text-align: center;
    color: white;
    font-size: calc(1.2vw + 1vh + 2vmin);
   white-space: wrap;
}

.sidebarM {
    display: none;
    
}

.spline {
    display: block;
}

.BooostWord {
    font-size: 3.5rem;
}

.splineM {
    display: none;
}
@media screen and (max-width: 900px) {
   
    .BooostWord {
        font-size: calc(2.5vw + 2.5vh + 2vmin);
    }
    .section1 {
        height: 220vh;
        padding-top: 1rem ;
        padding-bottom: 1rem;
    }

    .BadgesContainer {
        flex-direction: column !important;
        width: 100%;
       
    }
    .BadgesPops {
        width: 80% !important;
        margin-bottom: 2rem;
    }

    .mainTitle {
        margin-top: 6%;
        margin-bottom: 8%;
        font-size: calc(2.3vw + 2.3vh + 2vmin);
    }
    .BobbyContainer {
        height: 0;
        display: none;
    }
    .spline {
        display: none;
    }
    .splineM {
        display: block;
    }
    .HomeBobby { 
        flex-direction: column;
        justify-content: center;
        align-items: center;
       
    }
    .BobbyCarousel {
        width: 80%;
        margin-left: -1rem;
    }
    .HomeSection {
        margin-top: 5rem;
        height: 75vh !important;
        padding: 0;
    }
    .bobbyCentered {
        z-index: 1000 !important;
        
    }

    .slidersBackground {
        width: 88% !important;
    }

}


@media screen and (max-width: 700px) and (orientation: portrait) {
    .splineM {
        display: block;
    }
    .spline {
        display: none;
    }

    .sidebar {
        display: none;
    }
    .sidebarM {
    display: flex;
    flex-direction: column;
    z-index: 100;
    background: linear-gradient(-45deg, #00ff00fd, #18c305, #169000, #ffffff);
    border-bottom: 2px solid var(--green1);
	background-size: 400% 400%;
	animation: gradient 25s ease infinite;
    color: #ffffff;
    height:80vh;
    width: 100px;
    
    transition: all 0.5s;
    position: fixed;
    top: 0;
    left: 0;

    
    
 
    align-items: center;
    justify-content: space-evenly;
    align-content: center;
    align-self: center;
    backdrop-filter: blur(4px);
   
    border-bottom-right-radius: 0.5rem;
    }
}

@media screen and (max-width: 500px) {
    .BobbyContainer {
        width: 100%;
        
    }
    .BobbyCarousel {
        width: 80%;
        padding: 0;
    }
    .HomeBobby { 
        padding-left: 0;
    }
}

.SplineForKorg {
    position: absolute;
    width: 100%;
    height:100%;
    top: 0%;
    left: 0%;
    z-index: 1000;
  }
  

  .BuyNow {
    width: 6rem;
    height: 30px;

    text-align: center;
    background: #0EBF40;
    color: #053312;
    border: none;
    font-weight: 900;
    transition: all 300ms ease-in-out;
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    padding: 0.3em;
    font-family: 'Oswald', sans-serif;
  }
  
  .BuyNowAlt {
    width: 6rem;
    height: 30px;
   
    text-align: center;
    background: rgba(14, 14, 14, 0);
    border: 1px solid cyan;
   
    font-weight: 900;
    transition: all 300ms ease-in-out;
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    padding: 0.3em;
