.BadgesContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 2rem;
    width: 100%;
    height: 100%;
    justify-content: space-around;
   align-content: center;
}

.BadgesPops {
    display: flex;
    flex-direction: column;
    width: 25%;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    height: 85%;
}

  .AboutBadges {
    width: 100%;
    height: 100%;
    background: #00000088;
    border-radius: 1rem;
    backdrop-filter: blur(5px);
    border: 2px solid #037503;
  }

  .imgBadges {
    width: 180px;
    height: 180px;

  }

  .sideSection {
    background-image: none;
  }